import React from 'react';
import { Authenticator, useAuthenticator } from '@aws-amplify/ui-react';
import '@aws-amplify/ui-react/styles.css';
import { Card, CardTitle, CardSubtitle, Button } from 'reactstrap';

function Signin() {
    const auth = useAuthenticator();

    return (
        <Authenticator>
            {({ user }) => (
                <Card style={{ maxWidth: '400px', margin: 'auto', marginTop: '20px' }}>
                    <CardTitle>Signed in</CardTitle>
                    {user && <CardSubtitle>Hello {user.attributes.email}</CardSubtitle>}
                    <Button className="ma-3" tag="a" href="/">
                        Continue to app
                    </Button>
                    <Button className="ma-2" onClick={() => auth.signOut()}>
                        Sign out
                    </Button>
                </Card>
            )}
        </Authenticator>
    );
}

export default Signin;
